<div class="hello"><div class="ui container" v-if="users &amp;&amp; toList(users).length &gt; 0 &amp;&amp; groups[$route.params.idx]"><div class="ui grid"><div class="ui one column row" v-if="groups[$route.params.idx].n"><div class="ui column ui segment"><h3>〈{{groups[$route.params.idx].n}}〉</h3><p>{{groups[$route.params.idx].intro}}<br class="thin-only">&nbsp;&nbsp;&nbsp;&nbsp;<a @click="toggleEdit($route.params.idx)"><i class="edit icon"></i>{{edit ? '結束' : ''}}編輯社團資料</a></p><p><router-link class="ui basic green button" to="/groups"><i class="globe icon"></i>探索所有社團</router-link></p><div class="ui form" v-show="edit"><div class="field" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>請先登入</button></div><div class="field"><div class="ui labeled input"><label class="ui label">輸入社團簡介  </label><input type="text" v-model="newIntro" placeholder="請先輸入社團簡介"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newIntro}" @click="addIntro($route.params.idx)">更新簡介</a></div></div><div class="ui grid"><div class="row"><p>成員：<span v-for="m in groups[$route.params.idx].members"><router-link :to="'/flag/' + m" v-if="users[m]"><img class="ui avatar" :src="users[m].photoURL" alt="users[m].n"></router-link></span><span v-show="uid"><a class="ui green tiny button" v-show="!isMember(groups[$route.params.idx].idx)" @click="join(groups[$route.params.idx].idx)">我要加入</a><a class="ui red tiny button" v-show="isMember(groups[$route.params.idx].idx)" @click="out(groups[$route.params.idx].idx)">我要退出</a></span></p></div><div class="two column stackable row"><div class="column"><div class="ui divided list"><div class="item left aligned">資源：</div><div class="item left aligned" v-for="(r, index) in groups[$route.params.idx].res" :key="index + r.n + r.href"><a :href="r.href" target="_blank" rel="noopener noreferrer"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a></div><div class="item ui form" v-show="uid &amp;&amp; edit"><div class="field"><div class="ui labeled input"><label class="ui label">輸入資源名</label><input type="text" v-model="newResName" placeholder="請先輸入資源名"></div></div><div class="field"><div class="ui labeled input"><label class="ui label">輸入資源網址</label><input type="text" v-model="newHref" placeholder="請先輸入資源網址"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newHref || !newResName}" @click="addRes($route.params.idx)">新增資源</a></div></div></div></div><div class="column">留言：<div class="ui divided list"><div class="item" v-for="(c, index) in latestChats" :key="index"><img class="ui avatar" :src="c.photoURL">{{c.n}} : {{c.t}}</div><div class="item ui form" v-if="uid"><div class="field"><div class="ui labeled input"><img class="ui avatar" :src="photoURL"><input class="input" v-model="msg" placeholder="在想什麼嗎?"><a class="ui label green button" :class="{disabled: !msg}" @click="addChat($route.params.idx)">留言  </a></div></div></div></div></div></div></div></div></div></div></div></div>